
import { Options, Vue } from "vue-class-component";

@Options({
  //   components: { StatusBar, Map, Ruler },
})
export default class TheRabbit extends Vue {
  rotate = 90;
  isrednoze = false;
  mounted() {
    setInterval(() => {
      if (this.rotate == 360) {
        this.rotate = 0;
        return;
      }
      this.rotate++;
      this.isrednoze = !this.isrednoze;
    }, 3);
  }
}
